import { LitElement, html, css } from "lit";

class MatterMap extends LitElement {
  static get styles() {
    return css`
      :host {
        display: block;
        font-family: Source Sans Pro, sans-serif; 
      }
    `;
  }

  static get properties() {
    return {
      configuration: { type: String },
      id: { type: String },
      _responseConfiguration: { type: Object },
    };
  }

  constructor() {
    super();
    this.libreriasCargadas = false;
  }

  // Se ejecuta una nica vez al comienzo.
  firstUpdated() {
    this.addMapea();
    this.secondUpdated();
  }

  // Cada vez que se actualiza algn parmetro de entrada en la llamada del matter-map y no ha sido la primera vez, entra en esta funcin.
  secondUpdated() {
    this.updatedResponseConfiguration();
  }

  // Cada vez que se modifique algn parmetro de entrada, entra aqu.
  updated() {

    // En el caso de que ya este creado de antes, se elimina la variable div id"map" y se cargan los datos de nuevo
    if (document.getElementById("map_" + this.id) !== null) {
      this._responseConfiguration = undefined;

      var elem = document.getElementById("map_" + this.id);
      elem.parentNode.removeChild(elem);

      this.secondUpdated();

      // Si existen datos en responseData, respondeConfiguration y se han cargado los 3, se actualiza el setParametrs
    } else {
      if (this._responseConfiguration !== undefined && this.libreriasCargadas) {
        this.setParametersMapea();
      }
    }
  }

  // En esta funcin lo nico que se hace es parsear el JSON/url que se le introduce de configuracion, metindolo en la variable this._responseConfiguracion en forma de objeto.
  updatedResponseConfiguration() {
    const conf = this.configuration.substr(0, 4);
    const id = this.id;
    if (this.configuration !== null && this.configuration !== "undefined") {
      if (conf === "http") {
        fetch(this.configuration).then(response => {
          if (response.ok) {
            response.json().then(json => {
              this._responseConfiguration = json;
            }).catch(error => {
              this._responseConfiguration = [{ error: `ERROR_Json: ${error.message}` }];
            });
          } else {
            this._responseConfiguration = [{ error: `Respuesta de red KO` }];
          }
        }).catch(error => {
          this._responseConfiguration = [{ error: `ERROR_Fetch: ${error.message}` }];
        });
      } else {
        this.jsonSeccionesDetalleConf = JSON.parse(this.configuration);
        this._responseConfiguration = this.jsonSeccionesDetalleConf;
      }
    } else {
      this._responseConfiguration = null;
    }

    this.update();
  }


  // Se aaden las librerias de MAPEA desde dentro del wc.
  addMapea() {
    this.cont = 0;

    // console.log("CSS MAPEA - importing...");
    const mapeaStyles = document.createElement("link");
    mapeaStyles.href = "https://mapea4-sigc.juntadeandalucia.es/assets/css/mapea-6.3.0.ol.min.css";
    mapeaStyles.rel = "stylesheet";
    mapeaStyles.onload = this.contImportLibrerias();
    document.head.append(mapeaStyles);
    // console.log("CSS MAPEA - imported");

    this.head = document.getElementsByTagName("head")[0];

    var self = this;

    var loadScript = new Promise(function (resolve, reject, callback) {
      var scriptMinMapea = document.createElement('script');
      scriptMinMapea.src = "https://mapea4-sigc.juntadeandalucia.es/js/mapea-6.3.0.ol.min.js";
      scriptMinMapea.async = false;
      scriptMinMapea.type = 'text/javascript';
      scriptMinMapea.onload = function () {
        self.contImportLibrerias();
        document.onreadystatechange = function () {
          if (document.readyState == "complete" || document.readyState == "loaded") {
            resolve("resolve");
          } else {
            reject();
          }
        }
      };
      self.head.appendChild(scriptMinMapea);
    });

    loadScript.then(function (value) {
      if (value == "resolve") {
        var scriptConfMapea = document.createElement('script');
        scriptConfMapea.async = false;
        scriptConfMapea.src = "https://mapea4-sigc.juntadeandalucia.es/js/configuration-6.3.0.js";
        scriptConfMapea.type = 'text/javascript';
        scriptConfMapea.onload = self.contImportLibrerias();
        self.head.appendChild(scriptConfMapea);
        self.loadBaseLayerPlugin();
        self.loadSimpleLegendPlugin();
        self.secondUpdated();
      }
    });
  };


  loadBaseLayerPlugin(resolve, reject, callback) {
    var self = this;
    console.log("CSS PLUGIN MAPEA BASELAYER - importing...");
    const baseLayerStyles = document.createElement("link");
    baseLayerStyles.href = "https://emiliopardo.github.io/simplebaselayerselector/simplebaselayerselector.ol.min.css";
    baseLayerStyles.rel = "stylesheet";
    baseLayerStyles.onload = self.contImportLibrerias();
    document.head.append(baseLayerStyles);
    console.log("CSS PLUGIN MAPEA BASELAYER - imported");

    this.head = document.getElementsByTagName("head")[0];
    console.log("PLUGIN MAPEA BASELAYER - importing...");
    var scriptConfBaseLayerPlugin = document.createElement('script');
    scriptConfBaseLayerPlugin.src = "https://emiliopardo.github.io/simplebaselayerselector/simplebaselayerselector.ol.min.js";
    scriptConfBaseLayerPlugin.async = false;
    scriptConfBaseLayerPlugin.type = 'text/javascript';
    scriptConfBaseLayerPlugin.onload = function () {
      self.contImportLibrerias();
      document.onreadystatechange = function () {
        if (document.readyState == "complete" || document.readyState == "loaded") {
          resolve("resolve");
        } else {
          reject();
        }
      }
    };
    self.head.appendChild(scriptConfBaseLayerPlugin);
    console.log("PLUGIN MAPEA BASELAYER - imported");
  };

  loadSimpleLegendPlugin(resolve, reject, callback) {
    var self = this;
    console.log("CSS PLUGIN MAPEA SIMPLELEGEND - importing...");
    const simplelegendStyles = document.createElement("link");
    simplelegendStyles.href = "https://emiliopardo.github.io/simplelegend/simplelegend.ol.min.css";
    simplelegendStyles.rel = "stylesheet";
    simplelegendStyles.onload = self.contImportLibrerias();
    document.head.append(simplelegendStyles);
    console.log("CSS PLUGIN MAPEA SIMPLELEGEND - imported");
    console.log("PLUGIN MAPEA SIMPLELEGEND - importing...");
    var scriptConfSimpleLegendPlugin = document.createElement('script');
    scriptConfSimpleLegendPlugin.src = "https://emiliopardo.github.io/simplelegend/simplelegend.ol.min.js";
    scriptConfSimpleLegendPlugin.async = false;
    scriptConfSimpleLegendPlugin.type = 'text/javascript';
    scriptConfSimpleLegendPlugin.onload = function () {
      self.contImportLibrerias();
      document.onreadystatechange = function () {
        if (document.readyState == "complete" || document.readyState == "loaded") {
          resolve("resolve");
        } else {
          reject();
        }
      }
    };
    self.head.appendChild(scriptConfSimpleLegendPlugin);
    console.log("PLUGIN MAPEA SIMPLELEGEND - imported");
  };


  // Mtodo que lleva la cuenta de cuantas librerias necesarias de mapea se han importado
  contImportLibrerias() {
    this.cont += 1;
    if (this.cont == 7) {
      this.libreriasCargadas = true;
    }
  };

  // Mtodo donde se crea el elemento div necesario para que funcione MAPEA y la leyenda
  setParametersMapea() {

    // Creamos el elemento div principal, necesario para mostrar el mapa
    const div = document.createElement("div");
    div.id = "map_" + this.id;
    div.style.cssText = 'position: relative; height:600px;';
    var aux_div = document.getElementById(this.id);
    aux_div.after(div);

    this.setInitialConfigurationMAPEA();
  };

  // Mtodo que inicializa la configuracin principal de MAPEA
  setInitialConfigurationMAPEA() {
    let arraylegendLayers = new Array()
    let arraybaseLayers = new Array()
    let arrayoverlaylayers = new Array()
    // Se crea la variable mapajs que es la que va a configurar toda la vista de MAPEA con una configuracion inicial ya preestablecida.
    // He puesto para que nada mas iniciar el mapa, salga sevilla, marcando el punto del centro de Sevilla.
    let baselayers = this._responseConfiguration.configuration.baselayers
    let overlaylayers = this._responseConfiguration.configuration.overlaylayers
    baselayers.forEach(element => {
      let layer = new M.layer.WMS({
        url: element.url,
        name: element.name,
        legend: element.legend,
        transparent: element.transparent,
        tiled: element.tiled,
        version: '1.1.0'
      }, {
        params: {
          layers: element.name,
          queryable: false
        }
      })
      layer.setLegendURL(element.urlLegend)

      arraybaseLayers.push(layer)
    });
    this.mapajs = window.M.map({
      container: "map_" + this.id,
      layers: arraybaseLayers,
      controls: this._responseConfiguration.configuration.controls,
      maxExtent: this._responseConfiguration.configuration.maxExtent,
      center: {
        x: this._responseConfiguration.configuration.initialcenter.coordinates[0],
        y: this._responseConfiguration.configuration.initialcenter.coordinates[1],
      },
      projection: "EPSG:25830*m",
      zoom: this._responseConfiguration.configuration.initialzoom
    });

    overlaylayers.forEach(element => {
      let layer = new M.layer.WMS({
        url: element.url,
        name: element.name,
        legend: element.legend,
        transparent: element.transparent,
        tiled: element.tiled,
        version: '1.1.0'
      }, {
        params: {
          layers: element.name,
          queryable: false,
          style: element.style,
        }
      })
      layer.setLegendURL(element.urlLegend)
      layer.setOpacity(0.7)
      arrayoverlaylayers.push(layer)
      if (element.showLegend == true) {
        arraylegendLayers.push(layer)
      }
    });

    this.mapajs.addLayers(arrayoverlaylayers)


    // CARGA Y CONFIGURACION DE PLUGINS

    const configSimplebaselayerselector = { displayBaseLayersInLayerSwitcher: false }
    const simplebaselayerselectorPlugin = new M.plugin.Simplebaselayerselector(configSimplebaselayerselector)
    this.mapajs.addPlugin(simplebaselayerselectorPlugin);

    const simpleLegend = new M.plugin.Simplelegend()
    this.mapajs.addPlugin(simpleLegend)
    simpleLegend.updateLegend(arraylegendLayers)
  };

  render() {
    return html`
      ${this._responseData !== null && this._responseConfiguration !== undefined ? html`
      ` : html``}`;
  }

}

if (!window.customElements.get("matter-map")) {
  window.customElements.define("matter-map", MatterMap);
}
